import React, { createRef, forwardRef, useState } from "react";
import { BufferGeometry } from "three/src/core/BufferGeometry";
import { useSphere } from "@react-three/cannon";
import { Sphere, Material, Vector3 } from "three";
import { useFrame } from "@react-three/fiber";
import { useKeyPress } from "../hooks";

export interface MeshWithCollisionProps {
  geometriesAndMaterials: [BufferGeometry, Material][],
  collisionGeometry: BufferGeometry,
  position?: number[],
  velocity?: number[],
  scale?: number
}

export const MeshWithCollision = forwardRef((props: any, ref?: any) => {
  const [lastPressed, setLastPressed] = useState(false)
  const happyPress = useKeyPress("h");


  const meshes = props.geometriesAndMaterials.map((item: any[]) => {
    return (
      <mesh geometry={item[0]} material={item[1]} scale={new Vector3(...Array(3).fill(props.scale || 1))}>
      </mesh>
    )
  })
  const collisionGeometry = props.collisionGeometry as BufferGeometry
  collisionGeometry.computeBoundingSphere();
  const boundingSphere = collisionGeometry.boundingSphere as Sphere;
  const radius = boundingSphere.radius as number;
  const d = 2.5
  const eps = [-d, d]
  ref = ref || createRef();
  const [ref2, api] = useSphere(() => ({
    position: props.position || [0 + getRandom(eps), 0 + getRandom(eps), 14.65 + getRandom(eps)],
    rotation: [getRandom([-Math.PI, Math.PI]), getRandom([-Math.PI, Math.PI]), getRandom([-Math.PI, Math.PI])],
    mass: 1,
    args: radius ** 2 / (radius ** 1.3) * (props.scale || 1),
    velocity: props.velocity,
    angularDamping: 0.99,
  }), ref)
  useFrame(() => {
    if (happyPress) {
      if (!lastPressed) {
        api.velocity.set(0, 0, 30);
        api.angularVelocity.set(10, 12, 13)
        setLastPressed(true)
      }
    } else {
      setLastPressed(false);
    }
  })
  return (
    <group ref={ref}>
      {meshes}
    </group>
  )
})

function getRandom(eps: number[]) {
  return Math.floor(Math.random() * eps[1] - eps[0])
}

