import React, { Suspense, useEffect, useRef, useState } from 'react';
import './App.css';
import { Canvas, useFrame, useThree } from '@react-three/fiber'
import Model from "./model/Gumball_separated";
import { Debug, Physics } from '@react-three/cannon'
import { Object3D, Vector3 } from "three";
import { Camera } from "./components/Camera";
import { useGLTF } from "@react-three/drei";
import { doConfetti, fireRandom, getReward } from "./utils";

function Overlay(props) {
  const rewardRef = useRef<Object3D>();
  const {camera} = useThree();
  const [ballPosition, setBallPosition] = useState(new Vector3(0, 0, 0));
  const [ballRotation, setBallRotation] = useState(0);
  const {nodes} = useGLTF('/gumball_separated.gltf') as any
  const item = [
    nodes.Object_1020,
    nodes.Object_1020_1,
  ]
  const [hovering, setHovering] = useState(false);

  useFrame(() => {
    const d = new Vector3(0, 0, 0)
    const c = new Vector3(0, 0, 0)
    camera.getWorldDirection(d);
    camera.getWorldPosition(c);
    d.normalize()
      .multiplyScalar(5);
    c.add(d)
    setBallPosition(c);
    setBallRotation(ballRotation + 0.005)
  })

  return (
    <group position={ballPosition}>
      <mesh ref={rewardRef} geometry={item[0].geometry} material={item[0].material}
            rotation={[0, 0.1 * ballRotation, ballRotation]}
            onPointerOver={(event) => setHovering(true)}
            onPointerOut={(event) => setHovering(false)}
            onClick={props.onBallClick}
      >
        <meshStandardMaterial color={hovering ? 'hotpink' : 'orange'} />
      </mesh>
      <mesh ref={rewardRef} geometry={item[1].geometry} material={item[1].material}
            rotation={[0, 0.1 * ballRotation, ballRotation]}/>
    </group>
  )
}

function OtherOverlay(props) {
  const {innerWidth: width, innerHeight: height} = window;

  return (
    <div
      style={{
        position: "absolute",
        width: "100vw",
        height: "100vh",
        backgroundColor: "rgba(0, 0, 0, 0.8)",
        color: "white",
        zIndex: 1
      }}
      onClick={(e) => doConfetti(e, width, height)}>
      <div style={{
        position: 'absolute', left: '50%', top: '50%',
        transform: 'translate(-50%, -50%)',
        fontSize: "50px",
        textAlign: "center"
      }}>
        {props.text || "Hello, world!"}
      </div>
    </div>
  )
}

function App() {
  Object3D.DefaultUp.set(0, 0, 1);
  const [overlayVisible, setOverlayVisible] = useState(false);
  const [overlay2Visible, setOverlay2Visible] = useState(false);
  const [overlayDelay, setOverlayDelay] = useState(false);
  const [confettiIntervals, setConfettiIntervals] = useState<any[]>([]);
  const [rewardText, setRewardText] = useState("Hello World!");

  const setConfetti = () => {
    const interval1 = setInterval(() => {
      fireRandom()
    }, 1000)

    const interval2 = setInterval(() => {
      fireRandom()
    }, 1645)
    setConfettiIntervals([interval1, interval2])
  }
  const onRewardClick = () => {
    setRewardText(getReward())
    setOverlay2Visible(true);
    setConfetti()
  }
  useEffect(() => {
    const aFunc = (e) => {
      if (e.key === "h" && !overlayDelay) {
        setOverlayDelay(true)
        setTimeout(() => {
          setOverlayVisible(true);
        }, 2000)
      }
    }
    const bFunc = (e) => {
      if (e.key === "e" && overlayDelay) {
        reset()
      }
    }
    window.addEventListener("keydown", aFunc);
    window.addEventListener("keydown", bFunc);

    return () => {
      window.removeEventListener('keydown', aFunc)
      window.removeEventListener('keydown', bFunc)
    }
  })
  const reset = () => {
    setOverlayDelay(false);
    setOverlayVisible(false);
    confettiIntervals.forEach(timeout => {
      clearInterval(timeout)
    })
    setConfettiIntervals([])
    setOverlay2Visible(false);
  }
  return (
    <>
      {overlay2Visible ? <OtherOverlay text={rewardText}/> : null}
      <Canvas style={{height: "100vh"}}>

        {overlayVisible ? <Overlay onBallClick={onRewardClick}/> : null}
        <ambientLight/>
        <pointLight position={[10, 10, 10]}/>
        <Suspense fallback={null}>
          <Physics gravity={[0, 0, -10]} defaultContactMaterial={{restitution: 0.5}}>
            <Debug color="black" scale={1.05}>
            </Debug>
            <Model/>
          </Physics>
        </Suspense>
        <Camera/>
      </Canvas>
    </>
  );
}

useGLTF.preload('/gumball_separated.gltf')

export default App;
