const key = "myCat";

export function whichFirstRewardToShow(): string {
  const firstRewards = getFirstRewards();
  const lastReward = localStorage.getItem(key);
  let out: string;
  if (lastReward !== null) {
    const lastRewardInt = parseInt(lastReward);
    if (lastRewardInt >= firstRewards.length) {
      return ""
    }

    out = firstRewards[ lastRewardInt + 1]
    localStorage.setItem(key, (lastRewardInt + 1).toString())
  } else {
    localStorage.setItem(key, "0");
    out = firstRewards[0];
  }
  return out
}

export function getReward() {
  //TODO set a limit for rewards already gotten
  //TODO set a limit for rolls
  const firstReward = whichFirstRewardToShow();
  if (firstReward) {
    return firstReward;
  }
  // higher number = more common
  const rewards: [string, number][] = [
    // rewards :)
    ['Literally skip the dishes', 100],
    ['Homecooked breakfast/lunch/dinner of your choice', 100],
    ['Restaurant of your choice', 100],
    ['Foot massage', 100],
    ['Back massage', 100],
    ['Full body massage', 60],
    ['Shoulders/back massage', 100],
    ["1 free hug", 100],
    ['Homecooked lunch', 100],
    ['Ride to work for 1 day', 50],
    ['Tiktok of your choice', 50],
    ['Breakfast in bed', 50],
    ["One day of no interruptions", 50],
    ["Discord nitro for 1 month (2 months if you meow)", 50],
    ['Special request', 40],
    ['Special flowers', 40],
    ["Jordan cleans your room", 40],
    ['Restaurant of my choice', 30],
    ['Ride to work for 5 days', 30],
    ['Ride to work for 10 days', 10],
    ['You get nothing', 10],
    ["Yes day", 10],
    ["No day", 10],
    ["Go fast eat ass", 1],
    ["5 gifted subs", 10],
    ["I will scream one word of your choice out my window", 10],

    // punishments :(
    ['Matching Halloween costumes of your choice', 100],
    ["You can put makeup on me", 40],
    ['Cosplay of your choice', 90],
    ["One day without me", 10],
    ["One day with me", 10],

    ['Ajay will pay for dinner of our choice (max $1 USD)', 10],
    ['Derek will pay for dinner of our choice (max $1)', 10],
    ['Bilal will pay for dinner of our choice (max $1)', 10],
    ['Stephen will pay for dinner of our choice (max $1)', 10],
    ['Khansa will pay for dinner of our choice (max $1)', 10],
    ['Aaron will pay for dinner of our choice (max $100) (he can come along)', 10],
  ];













  // First, we loop the main dataset to count up the total weight.
  // We're starting the counter at one because the upper boundary
  // of Math.random() is exclusive.
  let total = 0;
  for (let i = 0; i < rewards.length; ++i) {
    total += rewards[i][1];
  }

  // Total in hand, we can now pick a random value akin to our
  // random index from before.
  const threshold = Math.random() * total;

  // Now we just need to loop through the main data one more time
  // until we discover which value would live within this
  // particular threshold. We need to keep a running count of
  // weights as we go, so let's just reuse the "total" variable
  // since it was already declared.
  total = 0;
  for (let i = 0; i < rewards.length - 1; ++i) {
    // Add the weight to our running total.
    total += rewards[i][1];

    // If this value falls within the threshold, we're done!
    if (total >= threshold) {
      return rewards[i][0] + "!";
    }
  }

  // Wouldn't you know it, we needed the very last entry!
  return rewards[rewards.length - 1][0] + "!";
}